.salt-theme.tar-theme {
  --salt-actionable-cursor-hover: pointer;
  --salt-actionable-cursor-active: pointer;
  --salt-actionable-cursor-disabled: not-allowed;

  /* Primary variant */
  --salt-actionable-primary-foreground: var(
    --salt-palette-interact-primary-foreground
  );
  --salt-actionable-primary-foreground-hover: var(
    --salt-palette-interact-primary-foreground-hover
  );
  --salt-actionable-primary-foreground-active: var(
    --salt-palette-interact-primary-foreground-active
  );
  --salt-actionable-primary-foreground-disabled: var(
    --salt-palette-interact-primary-foreground-disabled
  );
  --salt-actionable-primary-background: var(
    --salt-palette-interact-primary-background
  );
  --salt-actionable-primary-background-hover: var(
    --salt-palette-interact-primary-background-hover
  );
  --salt-actionable-primary-background-active: var(
    --salt-palette-interact-primary-background-active
  );
  --salt-actionable-primary-background-disabled: var(
    --salt-palette-interact-primary-background-disabled
  );

  /* CTA variant */
  --salt-actionable-cta-foreground: var(--salt-palette-interact-cta-foreground);
  --salt-actionable-cta-foreground-hover: var(
    --salt-palette-interact-cta-foreground-hover
  );
  --salt-actionable-cta-foreground-active: var(
    --salt-palette-interact-cta-foreground-active
  );
  --salt-actionable-cta-foreground-disabled: var(
    --salt-palette-interact-cta-foreground-disabled
  );
  --salt-actionable-cta-background: var(--salt-palette-interact-cta-background);
  --salt-actionable-cta-background-hover: var(
    --salt-palette-interact-cta-background-hover
  );
  --salt-actionable-cta-background-active: var(
    --salt-palette-interact-cta-background-active
  );
  --salt-actionable-cta-background-disabled: var(
    --salt-palette-interact-cta-background-disabled
  );

  /* Secondary variant */
  --salt-actionable-secondary-foreground: var(
    --salt-palette-interact-secondary-foreground
  );
  --salt-actionable-secondary-foreground-hover: var(
    --salt-palette-interact-secondary-foreground-hover
  );
  --salt-actionable-secondary-foreground-active: var(
    --salt-palette-interact-secondary-foreground-active
  );
  --salt-actionable-secondary-foreground-disabled: var(
    --salt-palette-interact-secondary-foreground-disabled
  );
  --salt-actionable-secondary-background: var(
    --salt-palette-interact-secondary-background
  );
  --salt-actionable-secondary-background-hover: var(
    --salt-palette-interact-secondary-background-hover
  );
  --salt-actionable-secondary-background-active: var(
    --salt-palette-interact-secondary-background-active
  );
  --salt-actionable-secondary-background-disabled: var(
    --salt-palette-interact-secondary-background-disabled
  );
}
