.salt-theme.tar-theme {
  --salt-selectable-cursor-hover: pointer;
  --salt-selectable-cursor-selected: pointer;
  --salt-selectable-cursor-blurSelected: pointer;
  --salt-selectable-cursor-disabled: not-allowed;
  --salt-selectable-cursor-readonly: not-allowed;

  --salt-selectable-borderStyle: solid;
  --salt-selectable-borderStyle-hover: solid;
  --salt-selectable-borderStyle-selected: solid;
  --salt-selectable-borderStyle-blurSelected: solid;

  --salt-selectable-borderColor: var(--salt-palette-interact-border);
  --salt-selectable-borderColor-hover: var(
    --salt-palette-interact-border-hover
  );
  --salt-selectable-borderColor-selected: var(
    --salt-palette-interact-border-active
  );
  --salt-selectable-borderColor-selectedDisabled: var(
    --salt-palette-interact-border-activeDisabled
  );
  --salt-selectable-borderColor-disabled: var(
    --salt-palette-interact-border-disabled
  );
  --salt-selectable-borderColor-readonly: var(
    --salt-palette-interact-border-readonly
  );

  --salt-selectable-foreground: var(--salt-palette-interact-foreground);
  --salt-selectable-foreground-disabled: var(
    --salt-palette-interact-foreground-disabled
  );
  --salt-selectable-foreground-hover: var(
    --salt-palette-interact-foreground-hover
  );
  --salt-selectable-foreground-selected: var(
    --salt-palette-interact-foreground-active
  );
  --salt-selectable-foreground-selectedDisabled: var(
    --salt-palette-interact-foreground-activeDisabled
  );
  --salt-selectable-background: var(--salt-palette-interact-background);
  --salt-selectable-background-hover: var(
    --salt-palette-interact-background-hover
  );
  --salt-selectable-background-selected: var(
    --salt-palette-interact-background-active
  );
  --salt-selectable-background-blurSelected: var(
    --salt-palette-interact-background-blurSelected
  );
  --salt-selectable-background-disabled: var(
    --salt-palette-interact-background-disabled
  );
  --salt-selectable-background-selectedDisabled: var(
    --salt-palette-interact-background-activeDisabled
  );
}
