.salt-theme.tar-theme {
  --tar-color-black: black;
  --tar-color-white: white;
  --tar-color-white-fade-50: rgba(255, 255, 255, 0.5);

  --tar-color-blue-60: #0097cc; /*  #0097CC (lagoon) */

  --tar-color-red-10: rgb(230, 0, 0); /* #E60000*/
  --tar-color-red-20: rgb(189, 0, 12); /* /* #BD000C */
  --tar-color-red-30: rgb(247, 128, 128); /* #F78080 */
  --tar-color-red-40: rgba(230, 0, 0, 0.502); /* #e6000080 */

  --tar-color-gray-03: rgb(238, 238, 238); /*  #EEE */
  --tar-color-gray-04: rgb(230, 230, 230); /*  #E6E6E6 (mercury) */
  --tar-color-gray-05: rgb(215, 215, 215); /*  #D7D7D7 */
  --tar-color-gray-10: rgb(170, 170, 170); /*  #aaa */
  --tar-color-gray-15: rgb(190, 190, 190); /* #BEBEBE */
  --tar-color-gray-20: rgb(204, 204, 204); /*  #ccc */
  --tar-color-gray-25: rgb(145, 145, 145); /*  #919191*/
  --tar-color-gray-28: rgb(120, 120, 120); /*  #787878*/
  --tar-color-gray-30: rgb(68, 68, 68); /*  #444 (tundora)*/
  --tar-color-gray-35: rgb(91, 91, 91); /*  #5b5b5b */
  --tar-color-gray-40: rgb(100, 100, 100); /*  #646464*/
  --tar-color-gray-65: rgb(55, 55, 55); /* #373737 */
  --tar-color-gray-67: rgb(38, 38, 38); /* #262626 */
  --tar-color-gray-68: rgb(41, 41, 41); /* #292929 */
  --tar-color-gray-70: rgb(28, 28, 28); /* #1C1C1C */
}
