.salt-theme.tar-theme {
  --salt-navigable-cursor-active: pointer;
  --salt-navigable-cursor-hover: pointer;
  --salt-navigable-cursor-disabled: not-allowed;
  --salt-navigable-cursor-edit: text;

  --salt-navigable-fontWeight: var(--salt-typography-fontWeight-regular);
  --salt-navigable-fontWeight-hover: var(--salt-typography-fontWeight-regular);
  --salt-navigable-fontWeight-active: var(
    --salt-typography-fontWeight-semiBold
  );
  --salt-navigable-fontWeight-edit: var(--salt-typography-fontWeight-regular);

  --salt-navigable-indicator-hover: var(
    --salt-palette-navigate-indicator-hover
  );
  --salt-navigable-indicator-active: var(
    --salt-palette-navigate-indicator-active
  );

  --salt-navigable-background-hover: var(
    --salt-palette-navigate-primary-background-hover
  );

  /* Link */
  --salt-navigable-textDecoration: underline;
  --salt-navigable-textDecoration-hover: none;
  --salt-navigable-textDecoration-selected: underline;
}
