.salt-theme.tar-theme {
  --salt-editable-cursor-hover: text;
  --salt-editable-cursor-active: text;
  --salt-editable-cursor-disabled: not-allowed;
  --salt-editable-cursor-readonly: text;

  --salt-editable-borderStyle: solid;
  --salt-editable-borderStyle-hover: solid;
  --salt-editable-borderStyle-active: solid;
  --salt-editable-borderStyle-disabled: solid;
  --salt-editable-borderStyle-readonly: solid;
  --salt-editable-borderWidth-active: 2px;

  --salt-editable-borderColor: var(--salt-palette-interact-border);
  --salt-editable-borderColor-active: var(
    --salt-palette-interact-border-active
  );
  --salt-editable-borderColor-disabled: var(
    --salt-palette-interact-border-disabled
  );
  --salt-editable-borderColor-hover: var(--salt-palette-interact-border-hover);
  --salt-editable-borderColor-readonly: var(
    --salt-palette-interact-border-readonly
  );

  --salt-editable-primary-background: var(
    --salt-palette-neutral-primary-background
  );
  --salt-editable-primary-background-active: var(
    --salt-palette-neutral-primary-background
  );
  --salt-editable-primary-background-disabled: var(
    --salt-palette-neutral-primary-background-disabled
  );
  --salt-editable-primary-background-hover: var(
    --salt-palette-neutral-primary-background
  );
  --salt-editable-primary-background-readonly: var(
    --salt-palette-neutral-primary-background-readonly
  );

  --salt-editable-secondary-background: var(
    --salt-palette-neutral-secondary-background
  );
  --salt-editable-secondary-background-active: var(
    --salt-palette-neutral-secondary-background
  );
  --salt-editable-secondary-background-disabled: var(
    --salt-palette-neutral-secondary-background-disabled
  );
  --salt-editable-secondary-background-hover: var(
    --salt-palette-neutral-secondary-background
  );
  --salt-editable-secondary-background-readonly: var(
    --salt-palette-neutral-secondary-background-readonly
  );

  --salt-editable-help-fontStyle: italic;
}
