.salt-theme.tar-theme[data-mode="light"] {
  --salt-palette-neutral-primary-background: var(--tar-color-white);
  --salt-palette-neutral-primary-background-disabled: var(--vuu-color-gray-30);
  --salt-palette-neutral-primary-background-readonly: var(
    --tar-color-white-fade-background-readonly
  );
  --salt-palette-neutral-primary-foreground: var(--tar-color-gray-70);
  --salt-palette-neutral-primary-foreground-disabled: var(
    --salt-color-gray-900-fade-foreground
  );
  --salt-palette-neutral-primary-separator: var(--tar-color-gray-05);
  --salt-palette-neutral-primary-border: var(--tar-color-gray-40);
  --salt-palette-neutral-primary-border-disabled: var(
    --salt-color-gray-50-fade-border
  );
  --salt-palette-neutral-secondary-background: var(--vuu-color-gray-25);
  --salt-palette-neutral-secondary-background-disabled: var(
    --salt-color-gray-20-fade-background
  );
  --salt-palette-neutral-secondary-background-readonly: var(
    --salt-color-gray-20-fade-background-readonly
  );
  --salt-palette-neutral-secondary-border: var(--vuu-color-gray-50);
  --salt-palette-neutral-secondary-border-disabled: var(
    --salt-color-gray-50-fade-border
  );
  --salt-palette-neutral-secondary-foreground: var(--tar-color-gray-40);
  --salt-palette-neutral-secondary-foreground-disabled: var(
    --salt-color-gray-200-fade-foreground
  );
  --salt-palette-neutral-backdrop: var(--tar-color-white-fade-backdrop);
  --salt-palette-neutral-secondary-separator: var(--vuu-color-gray-05);
  --salt-palette-neutral-tertiary-separator: var(--tar-color-gray-03);
}

.salt-theme.tar-theme[data-mode="dark"] {
  --salt-palette-neutral-primary-background: var(--tar-color-gray-70);
  --salt-palette-neutral-primary-background-disabled: var(
    --salt-color-gray-800-fade-background
  );
  --salt-palette-neutral-primary-background-readonly: var(
    --salt-color-gray-800-fade-background-readonly
  );
  --salt-palette-neutral-primary-border: var(--tar-color-gray-25);
  --salt-palette-neutral-primary-border-disabled: var(
    --salt-color-gray-300-fade-border
  );
  --salt-palette-neutral-primary-foreground: var(--tar-color-white);
  --salt-palette-neutral-primary-foreground-disabled: var(
    --tar-color-white-fade-foreground
  );
  --salt-palette-neutral-primary-separator: var(--tar-color-gray-30);
  --salt-palette-neutral-secondary-background: var(--tar-color-gray-67);
  --salt-palette-neutral-secondary-background-disabled: var(
    --salt-color-gray-600-fade-background
  );
  --salt-palette-neutral-secondary-background-readonly: var(
    --salt-color-gray-600-fade-background-readonly
  );
  --salt-palette-neutral-secondary-border: var(--salt-color-gray-300);
  --salt-palette-neutral-secondary-border-disabled: var(
    --salt-color-gray-300-fade-border
  );
  --salt-palette-neutral-secondary-foreground: var(--tar-color-gray-10);
  --salt-palette-neutral-secondary-foreground-disabled: var(
    --salt-color-gray-70-fade-foreground
  );
  --salt-palette-neutral-backdrop: var(--salt-color-black-fade-backdrop);
  --salt-palette-neutral-secondary-separator: var(
    --tar-color-white-fade-separatorOpacity-secondary
  );
  --salt-palette-neutral-tertiary-separator: var(--tar-color-gray-68);
}
