.salt-theme.tar-theme {
  .saltCheckbox {
    --vuu-icon-color: var(--salt-selectable-foreground-selected);
    --vuu-icon-left: -1px;
    --vuu-icon-top: -1px;
  }

  .saltCheckbox:has(.saltCheckboxIcon-checked) {
    --checkbox-background: var(--salt-selectable-background-selected);
  }

  .saltCheckboxIcon {
    background: var(--checkbox-background);
    border-radius: 2px;
    border-style: solid;
    border-width: 1px;
  }

  .saltCheckboxIcon-checked.saltCheckboxIcon-disabled,
  .saltCheckbox:hover .saltCheckboxIcon-checked.saltCheckboxIcon-disabled {
    background-color: var(--salt-selectable-background-disabled);
  }

  .saltCheckboxIcon-checked:after {
    content: "";
    background-color: var(--vuu-icon-color);
    left: var(--vuu-icon-left, auto);
    --vuu-icon-size: calc(var(--salt-size-icon) + 2px);
    height: var(--vuu-icon-height, var(--vuu-icon-size, 12px));
    mask: var(--vuu-svg-tick) center center/var(--vuu-icon-size)
      var(--vuu-icon-size);
    -webkit-mask: var(--vuu-svg-tick) center center/var(--vuu-icon-size)
      var(--vuu-icon-size);
    mask-repeat: no-repeat;
    -webkit-mask-repeat: no-repeat;
    position: absolute;
    top: var(--vuu-icon-top, auto);
    width: var(--vuu-icon-width, var(--vuu-icon-size, 12px));
  }
}
